import "@logseq/libs"
import { logseq as PL } from "../package.json"

const onSettingsChange = () => {
  let colors: string[] = logseq.settings?.colors

  const vars: [string, string][] = colors.map((color, i) => [
    `--block-thread-color-level-${i + 1}`,
    color,
  ])

  const varsString = vars.map((pair) => pair.join(": ") + ";").join("\n")

  logseq.provideStyle({
    key: PL.id + "-vars",
    style: `:root { ${varsString} }`,
  })
}

const main = async () => {
  onSettingsChange()
  logseq.onSettingsChanged(onSettingsChange)

  let colors = logseq.settings?.colors
  // patches
  const appVersion = await logseq.App.getInfo("version")
  console.log("OOOO", logseq, colors, appVersion)
  if (appVersion) {
    logseq.provideStyle(`
      .block-children-left-border::after {
        content: '';
        position: absolute;
        left: 2px;
        height: 100%;
        width: 29px;

        background-color:
        opacity: .33;
      }

      ${Array.from(
        { length: logseq.settings?.maxDepth },
        (_, i) => `
          ${Array.from({ length: i + 1 }, () => ".block-children").join(" ")} {
            border-left-color: var(--block-thread-color-level-${
              (i + 1) % colors.length
            });
          }

          ${Array.from({ length: i + 1 }, () => ".block-children").join(
            " "
          )}-left-border::after {
            background-color: var(--block-thread-color-level-${
              (i + 1) % colors.length
            });
          }
      `
      )}
    `)
  }
}

console.log("initializing colored threads")

logseq
  .useSettingsSchema([
    {
      key: "colors",
      default: ["#008080", "#6e0772", "#008017", "#806600"],
      description:
        "Comma-separated CSS colors to highlight threads from left to right.",
      title: "Thread colors",
      inputAs: "textarea",
      type: "string",
    },
    {
      key: "maxDepth",
      default: 20,
      description: "Max indentation depth to generate CSS for.",
      title: "Max depth",
      type: "number",
    },
  ])
  .ready(main)
  .catch(console.error)
